.transactionHistory {
    width: 500px;
  
    font-weight: 400;
    text-align: center;
  
    background-color: rgb(119, 119, 160);
    border-radius: 8px;
    box-shadow: 0px 2px 6px gray;
  
    overflow: hidden;
  }
  
  .tableHead {
    font-size: 18px;
/*   
    background-color: rgb(155, 175, 200); */
  }
  
  .th {
    color: rgb(10, 60, 100);
    padding: 8px;
  }
  .td {
    padding: 8px;
  }
  
  .tableBody tr:nth-child(odd) {
    background-color: rgb(228, 241, 255);
  }
  
  .tableBody tr:nth-child(even) {
    background-color: rgb(232, 244, 255);
  }