.friendList {
  width: 500px;
  display: flex;
  gap: 20px;
  padding: 14px 12px;
  padding-top: 24px;
  
  height: fit-content;
  margin: auto;

  font-weight: 700;

  background-color: rgb(119, 119, 160);
  border: 2px solid #979ca5;
  border-radius: 8px;
  box-shadow: 0px 2px 6px gray;

  overflow: hidden;
}