.statistics {
  width: 500px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-top: 24px;
    padding: 12px 14px;
    align-items: center;
    height: fit-content;
  
    font-weight: 400;
  
    border-radius: 8px;
    box-shadow: 0px 2px 6px gray;
  
    overflow: hidden;
  }
  
  .title {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
  }
  
  .statisticList {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    width: 100%;

    
  }
  
  .item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 14px 12px;
    width: calc((100% - 12px * 4) / 5);
    background-color: rgb(119, 119, 160);
    
    border-radius: 12px;
  }
  
  .label {
    font-size: 16px;
  }
  
  .percentage {
    margin-top: 8px;
  
    font-size: 18px;
    font-weight: bold;
    color: rgb(10, 60, 100);
  }